.app{width:100%;height:100%;display:flex;flex-direction:column;background-color:transparent;position:relative}.app .query-row{display:flex;flex-direction:row;justify-content:center;padding:1rem 3rem}.app::before{content:"";position:absolute;width:100%;height:100%;top:0;left:0;z-index:-2;background:linear-gradient(to bottom, #162a3f, #020407)}

