.query-manager-wrapper{display:flex;flex-direction:column;align-items:center;flex-basis:50%;transition:flex-basis 1s 0s ease-out}.notification{background-color:rgba(29,55,82,0.5)}.notification div[class$="-message"]{font-size:.95rem;color:#fff}.buttons-wrapper{padding:.5rem 0;width:50%;display:flex;flex-direction:row;align-items:center;justify-content:space-around}.buttons-wrapper .apply-button{flex-basis:65%;color:#fff;background-color:#162a3f}.buttons-wrapper .apply-button:hover{background-color:#1d3752}.buttons-wrapper .reset-button{flex-basis:25%;color:#fff;border-color:#f185a0;background-color:#ec577d}.buttons-wrapper .reset-button:hover{border-color:#ec577d;background-color:#e62a59}

.year-selector-wrapper{display:flex;flex-direction:column;width:90%}.instruction{color:#fff;font-size:1rem;margin:.5rem 0}.input-wrapper{width:100%;display:flex;justify-content:center;align-items:center}.input-wrapper .year-input{width:60%;align-self:center;margin:1rem 0;border-radius:0}

