.mass-selector-wrapper{display:flex;flex-direction:column;width:90%;margin:1rem 0}.instruction{color:#fff;font-size:1rem;margin:.5rem 0}.input-wrapper{width:100%;display:flex;justify-content:center;align-items:center}.input-wrapper .mass-input{width:60%;align-self:center;margin:1rem 0;border-radius:0}

